var firstPlay = true;

async function doSaveStateInit(code) {
    if (code == "FR") {
        storageManager.find("SS" + code).then(crystalReady, e => initNewSaveState("FR"))
    } else if (code == "C") {
        storageManager.find("SS" + code).then(crystalReady, e => initNewSaveState("C"));
    } else if (code == "E") {
        storageManager.find("SS" + code).then(emeraldReady, e => initNewSaveState("E"));
    }
}

function initNewSaveState(code) {

    document.getElementById("emulator_target").style.visibility = "hidden";
    userInputEnabled = false;

    let func = async() => {

        IodineGUI.Iodine.NEXT_ROM = code;
        injectSaveCode = code;

        await delay(100);

        if (!IodineGUI.IOCore) {
            CommandExecutor.execute("Start");
        } else {
            CommandExecutor.execute("Restart");
        }

        await delay(200);

        CommandExecutor.execute("BigSpeedUp");

        var origialWarpRedirection = GameBoyAdvanceCPU.prototype.handleWarpRedirection;

        var inputLoopInterval;

        GameBoyAdvanceCPU.prototype.handleWarpRedirection = (address, romCode) => {
            IodineGUI.Iodine.pause();
            IodineGUI.Iodine.saveStateManager.saveState(romCode);
            clearInterval(inputLoopInterval);
            GameBoyAdvanceCPU.prototype.handleWarpRedirection = origialWarpRedirection;
            CommandExecutor.execute("BigSpeedUp");
            CommandExecutor.execute("Stop");
            userInputEnabled = true;


            if (romCode == "C") {
                crystalReady();
            } else if (romCode == "E") {
                emeraldReady();
            } else if (romCode == "FR") {
                fireRedReady();
            }

            injectSaveCode = null;
        }

        inputLoopInterval = setInterval(() => {CommandExecutor.execute("UpKeyDown"); CommandExecutor.execute("A")}, 100);
    }

    func();
}
var loadedAndStarted = false;
function initAndStart() {
    loadSaveStatePreviews();
    storageManager.find("lastLoadedRom").then(code => {startForCode(code);  delayedSaveStateLoad(code);}, () => {
        let loadCode = IodineGUI.Iodine.ROM_CODES.includes("FR") ? "FR" : IodineGUI.Iodine.ROM_CODES[0]
        storageManager.persist("lastLoadedRom", loadCode);
        startForCode(loadCode);
        loadedAndStarted = true;
    });
}

async function delayedSaveStateLoad(code) {

    if(loadedAndStarted) { return; }

    document.getElementById("emulator_target").style.visibility = "hidden";
    await delay(500);
    quickHideScreen();
    try {
        IodineGUI.Iodine.saveStateManager.loadState(code, true);
    } catch (e) {
        quickHideScreen();
        await delay(300);
        try {
            IodineGUI.Iodine.saveStateManager.loadState(code, true);
        } catch (e) {
            console.log("Couldn't automatically load autosave")
        }
    }

    IodineGUI.Iodine.saveStateManager.lastLoaded = code;
    loadedAndStarted = true;
    document.getElementById("emulator_target").style.visibility = "visible";
    IodineGUI.Iodine.play();
}


async function delayedSaveStateSave() {
    await delay(3500 / IodineGUI.Iodine.getSpeed());
    IodineGUI.Iodine.saveStateManager.saveState(IodineGUI.Iodine.IOCore.cartridge.romCode);
    IodineGUI.Iodine.saveStateManager.saveMultiState("LATEST");
    document.getElementById("reset").classList.remove("disabled");
    M.toast({html: 'Latest Save Updated', displayLength:1000 });
}

async function loadSaveStatePreviews() {
    ["MS1", "MS2", "MS3"].forEach(async ms => {
        try {
            let result = await storageManager.find(ms);
            document.getElementById("saveState" + ms[2] +"Preiew").src = result.preview;
        } catch(e) {
            // save state did not exist
        }
    });
}

GameBoyAdvanceSaves.prototype.saveIntercept = function(address, data) {
    if (address == 0 && data == 48) {
        delayedSaveStateSave();
    } 
}

function restartFromLastSave() {
    storageManager.find("LATEST")
                  .then(s => IodineGUI.Iodine.saveStateManager.loadMultiState("LATEST", () => IodineGUI.Iodine.restart()), IodineGUI.Iodine.restart());
}

function startForCode(code) {
    if (firstPlay) {
        IodineGUI.Iodine.NEXT_ROM = code;

        if (IodineGUI.Iodine.IOCore) {
            IodineGUI.Iodine.restart(); 
            IodineGUI.Iodine.saveStateManager.initStoredStates(IodineGUI.Iodine.ROM_CODES);
        } else {
            IodineGUI.Iodine.play();
            IodineGUI.Iodine.saveStateManager.initStoredStates(IodineGUI.Iodine.ROM_CODES);
        }

        firstPlay = false;
        document.getElementById("play").innerHTML = "<span>RESUME</span>"
    } else {
        IodineGUI.Iodine.play();
    }
}

function fireRedReady() {
    document.getElementById("fr_btn").classList.remove("disabled");
    document.getElementById("e_btn").classList.remove("disabled");
    document.getElementById("play").classList.remove("disabled"); 
    document.getElementById("fr-status").innerHTML = "(Ready)";  
    clearCanvas();
}

function crystalReady() {
    document.getElementById("fr_btn").classList.remove("disabled");
    document.getElementById("e_btn").classList.remove("disabled");
    document.getElementById("play").classList.remove("disabled"); 
    document.getElementById("c-status").innerHTML = "(Ready)";  
    clearCanvas();
}

async function clearCanvas() {
    let canvas = document.getElementById("emulator_target");
    let ctx = canvas.getContext('2d');
    await delay(300);
    ctx.clearRect(0, 0, canvas.width, canvas.height);
}

function emeraldReady() {
    document.getElementById("fr_btn").classList.remove("disabled");
    document.getElementById("c_btn").classList.remove("disabled");
    document.getElementById("play").classList.remove("disabled"); 
    document.getElementById("e-status").innerHTML = "(Ready)";  
    clearCanvas();
}

function exportLastSaveData() {
    storageManager.find("LATEST").then(s => {
        let saveData = saveImage.replace("${IMAGE}", s.preview);
        saveData += "\n";
        saveData += LZUTF8.compress(JSON.stringify(s), {outputEncoding: "StorageBinaryString"});
        let file = new File([saveData], "MultiGameSave.xgss", {type: "text/plain;charset=utf-8"});
        saveAs(file);
    });
}

function importLastSaveData() {
    let file = this.files[0];

    let reader = new FileReader();
    reader.readAsText(file,'UTF-8');

    reader.onload = readerEvent => {
        let content = readerEvent.target.result;
        content = content.substring(content.indexOf("\n") + 1); // Remove the first line icon
        content =  LZUTF8.decompress(content, {inputEncoding: "StorageBinaryString", outputEncoding: "String"});
        let multiSave = JSON.parse(content);
        storageManager.persist("LATEST", multiSave);
        storageManager.persist("SS" + "FR", multiSave.frSave);
        storageManager.persist("SS" + "C", multiSave.cSave);
        storageManager.persist("SS" + "E", multiSave.eSave);
        storageManager.persist("lastLoadedRom", multiSave.romCode);
        M.toast({html: 'Save Imported', displayLength:1000 });
     }
}

